.mask{
    width: 100%;
    height: 100vh;
    position: relative;
}
.p1,.h2{
   color: var(--second);

}
.bg-img{
    width: 100%;
    
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    height: 100%;
    object-fit: cover;
}
.mask::after{
    content: '';
    width: 100%;
    height: 100%;
    background-color: #000;
    position: absolute;
    top: 0;
    left: 0;
    opacity: .7;
}
.hero-sec .content{
    position: absolute;
    transform: translate(-50%,-50%);
    top: 50%;
    left: 50%;
    text-align: center;
}
.hero-sec .content h2{
    font-size: 4rem;
    padding: 0.6rem 1rem;
}
.hero-sec .content p{
    font-size: 1.8rem;
}
.hero-sec .content .btn{
    margin: 0.6rem 0.6rem;
}
@media screen and (max-width:640px) {
    .hero-sec .content h2{
        font-size: 2.5rem;
       
    }
    .hero-sec .content p{
        font-size: 1.2rem;
       
    }
    .hero-sec .content .btn{
        font-size: 1.1rem;
        display: flex;
    }
}