
.progress-container{
  display: flex;
   width: 100%;
   
   padding: 10px 20px;
   border-radius: 7px;
 color: white;  
 margin-bottom: 1rem; 
}
.left{
    flex: 1;
    margin-top: 3rem;
    
}
.right{
    flex: 1;
   
    margin-right: 3rem;
}
.title-progress{
    background-color: var(--main-color);
    font-weight: 400;
    color: var(--text);
    border: 2px solid var(--border);
    width: 100%;
    margin-top: 15px;
    margin-bottom: 30px;
    font-weight: bold;
}
.skill-box{
    width: 100%;
    margin: 25px 0;
    
}
.title{
    display: block;
    font-size: 14px;
    font-weight: 600;
    color: var(--text);
}
.skill-box .skill-bar{
    width: 100%;
    height: 8px;
    background-color: rgba(0, 0, 0, 0.1);
}
.skill-box .skills-per{
    position: relative;
    display: block;
    height: 100%;
    width: 95%;
    border-radius: 6px;
    background-color: gray;
    animation: progress 0.4s ease-in-out forwards;
    opacity: 0;
}
.skills-per.css{
    animation-delay: o.1s;
    width: 80%;
}
.skills-per.javascript{
    animation-delay: 0.2s;
    width: 60%;
}
.skills-per.html{
    width: 90%;
}
.skills-per.ReactJs{
    animation-delay: 0.3s;
    width: 70%;
}
.skills-per.NodeJs{
    width: 40%;
    animation-delay: 0.3s;
}
@keyframes progress {
    0%{
        width: 0;
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
    
}
.skills-per .tooltip{
    position: absolute;
    right: -14px;
    top: -28px;
    background-color: crimson;
    z-index: 1;
    font-size: 8px;font-weight: 500;
    border-radius: 3px;
    padding: 2px 6px;
}
.tooltip::before{
    content: '';
    position: absolute;
    left: 50%;
    bottom: -2px;
    height: 10px;
    width: 10px;
    background-color: crimson;
    z-index: -1;
}
.skills-per.wordpress{
    width: 90%;
    animation-delay: .4s;
}
.skills-per.wix{
    width: 90%;
    animation-delay: .4s;
}
.skills-per.php{
    width: 70%;
    animation-delay: .4s;
}
@media screen and (max-width:640px){
    .left{
        margin-right: 1rem;
    }
    .right{
        margin-right: 1rem;
    }
    span{
        font-size: 1.2rem;
    }
    
}