.pricing{
    padding: 6rem 1rem;
    background-color: black;
    width: 100%;
}
.card-container{
    max-width: 1140px;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap: 1rem;
    margin: auto;
}
.card{
    text-align: center;
    padding: 4rem;
    background-color: gray;
    transition: .3s;
    border-radius: 10px;
    border: 1px solid whitesmoke;

}
.bar{
    margin-top: 0.5rem;
    width: 20%;
    background-color: white;
   height: 3px;
    text-align: center;
    margin-left: 6rem;
    display:block;
}
.card h3{
font-size: 1.6rem;
}
.card .btc{
    font-size: 3rem;
}
.card p{
    
    margin-bottom: 1rem;
}

.card:hover{
    background-color: black;
    text-shadow: 1px solid black;
    border: 1px solid white;
}

@media screen and (max-width:800px) {
    .card-container{
        display: grid;
        grid-template-columns: repeat(2,1fr);
        
        
    }
  
}
@media screen and (max-width:650px) {
    .card-container{
        display: grid;
        grid-template-columns: repeat(1,1fr);
    }
}
