.heading-img{
    background-color: rgba(0, 0, 0, 0.8);
    width: 100%;
    position: relative;
    height: 60vh;
   

}
.heading-img::after{
    content: '';
    background-image: url("https://images.unsplash.com/photo-1509718443690-d8e2fb3474b7?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=870&q=80");
   background-size: cover;
   background-position: center;
   background-repeat: no-repeat;
   width: 100%;
   height: 100%;
   z-index: -1;
   position: absolute;
   top: 0;
   left: 0;

}

.heading{
    width: 100%;
    height: 100%;
 display: flex;
 align-items: center;
 justify-content: center;
 text-align:center;
 flex-direction: column;
}
h4{
font-size: 2.4rem;
color: white;
}
.heading-img p{
    font-size: 1.4rem;
    color: white;
}
@media screen and (max-width:640px) {
    .hero-img h1{
        font-size: 2rem;
        }
        .heading-img p{
            font-size: 1.2rem; 
        }   
}