@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800&display=swap');
*{
  padding: 0%;
  margin: 0%;
  box-sizing: border-box;
}
body{
  font-family: 'poppins',sans-serif;
  background-color: var(--main-color);
  
}
:root {
  --main-color: black;
  --text:#fff;
  --border:#000;
  --second:#f2f2f2;
  --nav-back:rgba(0, 0, 0, 0.9);
}

.btn{
  padding:12px 32px;
  cursor: pointer;
  background-color: rgb(248, 217, 15);
  font-size: 1rem;
  border: 1px solid #fff;
  text-transform: uppercase;
  font-weight: 600;
  color: black;
}
.btn-light{
  color: #fff;
  background-color: transparent;
}

.btn:hover{
  background: rgba(255, 255, 255, 0.2);
  transition: .3s;
  color:#fff;
}
.about-me{
  text-align: center;
  
  border: 2px solid  var(--border);
  width: 100%;
  background-color: var(--main-color);
  color: var(--text);
  padding: 1rem;
  margin-bottom: 1rem;
}
.light-theme{
  --main-color: #fff;
  --text:#000;
  --border:#000;
  --nav-back:rgba(255,255,255,0.9);
}
.dark-theme{
  --main-color: #000;
  --text:#fff;
  --border:#fff;
  --nav-back:rgba(0, 0, 0, 0.9);
}
