.location{
    display: flex;
    margin-bottom: 1rem;
    
}
.phone{
   
    font-weight: bold;
    display: flex;
}
.email{
    display: flex;
    font-weight: bold;
}
.footer{
    width: 100%;
    padding: 6rem 0;
    background-color: var(--main-color);
    border-top: 1px solid var(--border);
}
.footer-container{
    max-width: 1140px;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-gap: 40px;
  margin-left: 3rem;
}
.footer .left{
    max-width: 100%;
    height: 100%;
    padding: 1rem;
    display: flex;
    flex-direction: column;
    margin-top: 1.8rem;

}

.footer .right{
    max-width: 100%;
    height: 100%;
    padding: 1rem;
    display: flex;
    flex-direction: column;

}
.footer .right p{
    line-height: 25px;
    font-size: 1rem;
    text-align: left;
    color: var(--text);
}
 .h3{
    font-size: 1rem;
    padding-bottom: 0.8rem;
    color: var(--text);
}
.social-icons{
    margin-top: 1rem;
}
.right{
    margin-top: 1rem;
}
@media screen and (max-width:640px) {
    .footer-container{
        grid-gap: 20px;
        display: grid;
        grid-template-columns: repeat(1,1fr);
    }
    .footer-container .left{
        padding: 0;
        font-size: 1.2rem;
    }
    .footer-container .left h3{
        margin-left: 1rem;
    }
    .location{
        margin-left: 1rem;
    }
    .footer p{
        font-size: 1rem;
    }
    
}