*{
    text-decoration: none;
    list-style: none;
}
.switch {
position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--main-color);
  -webkit-transition: .4s;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}

input:checked + .slider {
 background-color: black;
 box-shadow: 0 0 1px #2196F3;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196F3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}
.icon{
    margin-left: 8px;
    margin-top: 5px;
}
.moon{
    margin-left: 5px;
}

.header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1rem;
   position: fixed;
   width: 100%;
   height: 80px;
   z-index: 20;
   background: var(--main-color);
   
   transition: .5s;

}


.nav-items{
    display: flex;
    align-items: center;
    color: var(--color);
    
    
}
.nav-items li{
    padding: 0 1rem;
    color: var(--text);
}
.nav-items li a{
    font-size: 1.2rem;
    font-weight: 500;
    color: var(--text);
    

   
}
h1{
    color: var(--text);
}
.hamburger{
    display: none;
    z-index: 1000;
}
.header-bg{
    background-color: gray;
    opacity: .8;
    transition: .5s;
}
.nav-items.active{
    color: var(--color);
 }

@media screen and (max-width: 1040px) {
    .nav-items{
      flex-direction: column;
      justify-content: center; 
      align-items: center;
      width: 100%;
      height: 100vh;
       background: var(--nav-back);
       position: absolute;
       top: 1.5%;
       left: -100%;
       z-index: 3;
       transition: .5s;
       color: var(--text);
       scroll-behavior: none;
    }
    .nav-items.active{
       left: 0;
    }
    .nav-items li{
        padding: 1rem;
    }
    .nav-items li a{
        font-size: 2rem;
    }
    .hamburger{
        display: initial;
    }

     .first{
        transition: .2s;
     } 
     .second{
        transition: .3s;
     }
     .third{
        transition: .4s;
     }
     .fourth{
        transition: .5s;
     }
    
  }