.project-heading{
    padding: 1rem 0 1rem 0;
    text-align: center;
    color: var(--text);
    background-color: var(--main-color);

}
.project-container{
    width: 100%;
    color: var(--text);
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap: 40px;
    padding: 5rem;
    background-color: var(--main-color);
}
.project-card{
    padding: 1rem 1rem;
    background: var(--main-color);
    width: 350px;
    border-radius: 10px;
    border: 1px solid var(--border);
    color: var(--text);
}
.project-card img{
    width: 100%;
    height: 180px;
    text-align: center;

}
.project-title{
    padding: 1rem 0;
    color: var(--text);
    font-size: 1.2rem;
    
}
.pro-details p{
    font-size: 1rem;
    padding-bottom: 1rem;
    text-align: justify;
    color: var(--text);

}
.mega{
    text-align: center;
    color: var(--text);
    background-color: var(--main-color);
}
.p2{
    color: var(--text);
}
.pro-btns{
    display: flex;
    justify-content: space-between;
}
.pro-btns .btn{
   
    display: block;
    margin: auto 2rem;
    padding: 0.4rem 1rem;
}
@media screen and (max-width:800px){
    .project-container{
        display: grid;
        grid-template-columns: repeat(2,1fr);
       padding: 1rem;
    }
    .container{
        display: grid;
        grid-template-columns: repeat(1,1fr);
    }
}

@media screen and (max-width:640px){
    .project-container{
        display: grid;
        grid-template-columns: repeat(1,1fr);
        padding: 1rem;
        

    }
    .project-card{
        width: 370px;
        
    }
    
}

/* this is dyanamic work card */
.container{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap: 1rem;
    width: 100%;
    background-color: var(--main-color);
    padding: 2rem;
}
.box{
    width: 380px;
    height: 300px;
   background-color: transparent;
    transition: ease-in-out 8s ;
    background-position: top;
    border-radius: 10px;
    border: 1px solid gray;
    text-align: center;
}
.box1{
    background-image: url("../assets/rentacar.png");
    
    background-size: cover;
    background-repeat: no-repeat;
    text-align: center;
}
.box2{
    background-image: url("../assets/xadautos.png");
    
    background-size: cover;
    background-repeat: no-repeat;
}
.box:hover{
    background-position: bottom;
}
.box3{
    background-image: url("../assets/saheefa.png");
    background-size: cover;
    background-repeat: no-repeat;
}
h2,p{
text-align: center;
color: var(--text);
}
h2{
    font-size: 1.4rem;
    padding: 10px;
    color: var(--text);
}

.pro-btns{
    display: flex;
    justify-content: space-between;
    padding: 2rem;
}
.btn{
    padding: 5px 10px;

}
@media screen and (max-width:800px){
    .container{
        display: grid;
        grid-template-columns: repeat(2,1fr);
        padding: 1rem;
        grid-gap: 5px;
    }
    .box{
        width: 360px;
    }
}
@media screen and (max-width:640px){
    .container{
        display: grid;
        grid-template-columns: repeat(1,1fr);
        width: 280px;
        
    }
    .box1{
        margin-bottom: 1rem;
    }
    .box{
        width: 380px;
        height: 300px;
        
        
    }
    
    .pro-btns{
        display: flex;
        justify-content: space-between;
        padding: 1rem;
    }
    .box3{
        margin-top: 1.8rem;
    }
    .box-one{
        width: 350px;
        height: 300px;
        margin-top: 2.5rem;
        margin-bottom: 5rem;
        

    }
    
    
    .pro-btns{
        margin-bottom: 1px solid white;
    }
    .box2{
        margin-top: 7rem;

    }
}
