.about-content{
    display:grid;
    grid-template-columns: repeat(2,1fr);
    grid-gap: 2rem;
    padding: 5rem;
    align-items: center;
    position: relative;
    background-color: var(--main-color);

}
.left{
    flex: 1;
    
}
.right{
    flex: 1;
    position: relative;
}
.right img{
    width: 500px;
    height: 400px;
    border-radius: 20px;
    z-index: 10;
    position: relative;
}
.left h1{
    margin-bottom: 10px;
    font-size: 1.8rem;
    text-align-last: center;
    color: var(--text);

}
.left p{
    font-size: 1rem;
    margin-bottom: 1.8rem;
    color: var(--text);
}
.left .btn{
    text-align: center;
    margin-left: 10rem;
    padding: 10px 25px;
}
.back{
    width: 500px;
    height: 400px;
    background-color: gray;
    border-radius: 20px;
    position: absolute;
    top: -5%;
    right: -10px;
}
@media screen and (max-width:800px){
    .about-content{
        display: grid;
        grid-template-columns: repeat(2,1fr);
        grid-gap: 10px;
    }
    .right img{
        width: 400px;
    }
    .back{
        right: 10px;
        width: 400px;
    }
    .left{
        margin: 0;
        padding: 0;
        width: 100%;
    }
}
@media screen and (max-width:640px){
    .about-content{
        display: flex;
        flex-direction: column;
    }
    .right{
        margin-left: 0.5rem;
    }
    .right img{
        width: 380px;
        
        margin-top: 1.8rem;
        
    }
    
    .back{
        left: 0;
        width: 390px;
        margin-top: 1.8rem;
    }
    .about-content{
        padding: 2rem;
    }
    .left .btn{
        margin-left: 8rem;
    }
   
}