label{
color:var(--text);
margin-bottom: 0.5rem;
}
.form{
    display: flex;
    align-items: center;
    padding: 3rem;
    margin-top: -2rem;
    width: 100%;
    
}
label{
    color: var(--text);
}
input{
    border: 1px solid var(--border);
    color: var(--text);
}
.location1 p{
    font-size: 1.4rem;
    color: var(--text);
}
.email1 p{
    font-size: 1.4rem;
    color: var(--text);
}
.phone1 h3{
    font-size: 1.4rem;
    color: var(--text);
}
.right{
    flex: 1;
    margin-top: -0px;
}
.left{
    flex: 1;
   margin-top: -0px;
}
form{
    display: flex;
    flex-direction: column;
    margin: auto;
    padding: 4rem 1rem;
    max-width: 600px;

}
input{
    /* Add some basic styling */
    border-bottom: 1px solid var(--border);
    
  
    /* Add some hover effects */
    transition: all 0.3s;
  }
  
  
    input:hover, input:focus {
      border-bottom: 2px solid #555;
      
    }
form input,form textarea{
    padding: 10px 18px;
    color: var(--text);
    /* background-color: rgba(255, 255, 255, 0.1); */
    background: var(--main-color);
    
    font-size: 1.2rem;
    outline: none;
    margin-bottom: 1rem;
    border-bottom: 1px solid var(--border);
    border-top: none;
    border-left: none;
    border-right:none;
    
    transform: translate(-5px);
}
@media screen and (max-width:800px){
    .form{
        padding: 0;
        
    }
    .left{
        margin-left: 0rem;
    }
}
@media screen and (max-width:640px){
    .form{
        padding: 0;
        
    }
    .left{
        margin-left: 0rem;
        margin-top: 1rem;
    }
    .form{
        display: flex;
        flex-direction: column;
        margin-top: 3rem;
    }
}